import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginEOSModule } from '@cryptolions/ulm-eosio';
import { environment } from 'src/environments/environment';
import { JunglePageComponent } from '../routes/chain.routes/jungle.route/jungle.component';
import { SharedModule } from './shared.module';


const routes: Routes = [
  {
    path: '',
    component: JunglePageComponent
  }
];

@NgModule({
  declarations: [
    JunglePageComponent
  ],
  imports:[
    SharedModule,
    LoginEOSModule.forRoot(environment.ulm['jungle']),
    RouterModule.forChild(routes)
  ],
  exports: [RouterModule]
})
export class JungleModule {
}
